import { HEIGHT, WIDTH } from "./constants"
import { Ball, Bullet } from "./objects"
import { distance, randomIntFromRange } from "./utils"
import shoot from '../sounds/shoot.mp3'
import config from "./config"

export default class Engine {
    constructor({time = 90} = {}) {
        this.time = time
        this.sounds = {}
        this.canvas = document.querySelector('canvas')
        this.canvas.width = WIDTH
        this.canvas.height = HEIGHT
        this.context = this.canvas.getContext('2d')
        this.gameObjects = []
        this.mouse = {
            x: WIDTH / 2,
            y: HEIGHT / 2,
            radius: 5,
            draw: function (engine) {
                engine.context.beginPath()
                engine.context.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false)
                engine.context.fillStyle = 'red'
                engine.context.fill()
                engine.context.closePath()
            },
            update: function (engine) {
                for (let i = 0; i < engine.gameObjects.length; i++) {
                    if (distance(engine.mouse.x, engine.mouse.y, engine.gameObjects[i].x, engine.gameObjects[i].y) - engine.gameObjects[i].radius < 0) {
                        this.draw(engine)
                    }
                }
            }
        }
        this.status = 'init'
    }

    start() {
        this.setup()
        this.init()
        this.animate()
    }

    reset() {
        this.gameObjects = []
        this.time = 90
        this.status = 'running'
        
        config.resetScore()

        clearInterval(this.gameplay)
    }

    pause() {
        this.status = 'pause'
        clearInterval(this.gameplay)
    }

    play() {
        this.initGameObjects()
        this.status = 'running'
    }

    coundown() {
        if(this.time <= 0) return this.pause()
        this.time--
        const minute = Math.floor(this.time / 60);
        const second = (this.time % 60);
        const countdown = `${minute >= 10 ? minute : '0' + minute}:${second >= 10 ? second : '0' + second}`
        document.getElementById('countdown').innerHTML = countdown
    }

    // Animation Loop
    animate() {
        requestAnimationFrame(() => this.animate())

        this.refreshCanvas()

        this.render()
    }

    refreshCanvas() {
        this.context.clearRect(0, 0, this.canvas.width, this.canvas.height)
    }

    render() {
        for (let i = 0; i < this.gameObjects.length; i++) {
            this.gameObjects[i].update()
        }

        this.mouse.update(this)
    }

    createRandomBall() {
        const radius = randomIntFromRange(15, 40)
        const x = randomIntFromRange(radius, this.canvas.width - radius)
        const y = randomIntFromRange(radius, this.canvas.height - radius)
        
        let collsed = false

        for(let i = 0; i < this.gameObjects.length; i++) {
            if(distance(x, y, this.gameObjects[i].x, this.gameObjects[i].y) - (this.gameObjects[i].radius + radius) < 0) {
                
                collsed = true
                break;
            }
        }

        if(collsed) {
            this.createRandomBall()
        } else {
            const ball = new Ball({ x, y, radius, context: this.context })
            this.gameObjects.push(ball)
        }
    }

    setup() {
        this.sounds['shoot'] = new Audio(shoot);
    }

    init() {
        this.initGameEvents()
        this.initGameObjects()      
    }

    initGameObjects() {
        this.gameplay = setInterval(() => {
            this.createRandomBall()
            this.coundown()
        }, 1000)
    }

    initGameEvents() {
        this.canvas.addEventListener('mousemove', (event) => {
            this.mouse.x = event.offsetX
            this.mouse.y = event.offsetY
        })

        this.canvas.addEventListener('click', (event) => {
            if(this.status === 'pause') return

            const bullet = new Bullet({ x: event.offsetX, y: event.offsetY, context: this.context })
            for (let i = 0; i < this.gameObjects.length; i++) {
                this.gameObjects[i].checkShotted(bullet, this)
            }

            // this.gameObjects.forEach((gameObject) => {
            //     gameObject.checkShotted(bullet, this)
            // })
        })

        // window.addEventListener('resize', () => {
        //     this.canvas.width = WIDTH
        //     this.canvas.height = HEIGHT
        // })
    }
}