import config from "../config"
import { distance } from "../utils"

export class Ball {
  constructor({ x, y, context, radius, color = 'black' }) {
    this.x = x
    this.y = y
    this.c = context
    this.radius = radius
    this.color = color
    this.shotted = null
    this.hide = false
    this.scored = 0
  }

  draw() {
    this.c.beginPath()
    this.c.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false)
    this.c.fillStyle = this.color
    this.c.fill()
    this.c.closePath()
  }

  drawShotted() {
    if (this.shotted === null) return

    this.c.beginPath()
    this.c.arc(this.shotted.x, this.shotted.y, this.shotted.radius, 0, Math.PI * 2, false)
    this.c.fillStyle = this.shotted.color
    this.c.fill()
    this.c.closePath()

    const targetColors = ['purple', 'green', 'blue', 'yellow']

    let radius = this.radius

    for (let color of targetColors) {
      radius -= (this.radius / 5)
      this.c.beginPath()
      this.c.arc(this.x, this.y, radius, 0, Math.PI * 2, false)
      this.c.strokeStyle = color
      this.c.stroke()
      this.c.closePath()
    }

    this.c.fillStyle = 'red';
    this.c.font = '20px courier';
    this.c.fillText(`+ ${this.scored}`, this.x, this.y - this.radius);
  }

  update() {
    if (this.hide) return

    this.draw()
    this.drawShotted()
  }

  checkShotted(bullet, engine) {
    if (this.shotted) return;

    const space = distance(this.x, this.y, bullet.x, bullet.y)

    if (space - this.radius < 0) {
      this.shotted = bullet

      const score = Math.round(((this.radius - space) / this.radius) * 5)

      if(score === 5) {
        this.time++
      }

      this.scored = score

      config.addScore(score)

      engine.sounds['shoot'].currentTime = 0;
      engine.sounds['shoot'].play()

      setTimeout(() => {
        this.hide = true
      }, 1000)
    }
  }
}