class Config {
    constructor() {
        this.score = 0
        this.scoreEl = document.getElementById('score')
    }

    addScore(score = 1) {
        this.score += score
        this.scoreEl.innerHTML = this.score
    }

    resetScore() {
        this.score = 0
        this.scoreEl.innerHTML = this.score
    }
}

const config = new Config()

export default config